.flash {
  position: absolute;
  flex-direction: column;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.8);
  h1 {
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: 40px;
    text-align: center;
  }
  h3 {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
    text-align: center;
    font-size: 20px;
  }
  button {
    width: 4rem;
    height: 2rem;
    cursor: pointer;
    outline: none;
    background: black;
    color: white;
    border: none;
    margin: 10px 0;
    &:hover {
      background: white;
      border: 2px solid #101010;
      color: black;
    }
  }
}
